import uniRate from "../../components/uni-rate/uni-rate.vue"
export default {
	components: {
		uniRate
	},
	data() {
		return {
			list: {},
			photo: "", //导演图片
			actName: "", //导演
			name: "", //导演名字
			trailerIds: [], //演员
			image: [], //剧照
			cover: "",
			flag: false
		}
	},
	methods: {
		click(cover) {
			this.cover = cover
			console.log(cover);
			this.flag = true
		},
		clicks() {
			this.flag = false
		},
		role() {
			uni.request({
				url: `${this.$api}/search/staff/${this.list.id}/1?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.photo = res.data.data[0].photo
					this.name = res.data.data[0].name
					this.actName = res.data.data[0].actName
					// console.log(res.data.data[0]);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		trailerId() {
			uni.request({
				url: `${this.$api}/search/staff/${this.list.id}/2?qq=122212489`,
				method: 'POST',
				data: {},
				success: res => {
					this.trailerIds = res.data.data
					// console.log(this.trailerIds);
				},
				fail: () => {},
				complete: () => {}
			});
		},
		longpress() {
			let _this = this
			uni.showActionSheet({
				itemList: ['保存图片到本地'],
				success() {
					uni.downloadFile({
						url: _this.cover,
						success(res) {
							console.log(res);
							if (res.statusCode === 200) {
								uni.saveImageToPhotosAlbum({
									filePath: res.tempFilePath,
									success() {
										uni.showToast({
											title: '保存成功'
										})
									},
									fail() {
										uni.showToast({
											title: "保存失败"
										})
									}
								})
							}
						}
					})
				}
			})
		}
	},
	mounted() {
		this.role()
		this.trailerId()
	},
	onLoad(e) {
		this.list = JSON.parse(e.item)
		// console.log(this.list);
	}
}
